import { _decorator, Component, Sprite, Material, UITransform } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('RedDotController')
export class RedDotController extends Component {

    @property(Material)
    material: Material | null = null;

    start() {
        const uiTransform = this.node.getComponent(UITransform);
        if (uiTransform && this.material) {
            // this.material.setProperty('u_node_width', uiTransform.width);
            // this.material.setProperty('u_node_height', uiTransform.height);
            
            // 如果是 Sprite 组件
            const sprite = this.node.getComponent(Sprite);
            if (sprite) {
                sprite.material = this.material;
            }
        }
    }
}